import React, { useState } from 'react';

const AddProduct = ({ onAdd }) => {
  const [product, setProduct] = useState({
    name: '',
    brand: '',
    description: '',
    batchNumber: '',
    price: '',
    quantity: '',
    expiryDate: '',
    manufacturingDate: '',
    supplier: '',
    location: '',
    category: 'general'
  });

  const [showAdvanced, setShowAdvanced] = useState(false);

  // Generate preview SKU
  const generatePreviewSKU = () => {
    if (!product.name) return 'XXX-BATCH';
    const productPrefix = product.name.substring(0, 3).toUpperCase().replace(/[^A-Z]/g, 'X');
    const batchSuffix = product.batchNumber ? product.batchNumber.substring(0, 6).toUpperCase() : 'AUTO';
    return `${productPrefix}-${batchSuffix}`;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (product.name && product.price && product.quantity) {
      onAdd({
        ...product,
        price: parseFloat(product.price),
        quantity: parseInt(product.quantity)
      });
      setProduct({
        name: '',
        brand: '',
        description: '',
        batchNumber: '',
        price: '',
        quantity: '',
        expiryDate: '',
        manufacturingDate: '',
        supplier: '',
        location: '',
        category: 'general'
      });
    }
  };

  return (
    <div className="add-product">
      <h3>Add New Product Batch</h3>
      <form onSubmit={handleSubmit} className="product-form">
        <div className="form-section">
          <h4>Basic Information</h4>
          <div className="form-row">
            <input
              type="text"
              placeholder="Product Name *"
              value={product.name}
              onChange={(e) => setProduct({...product, name: e.target.value})}
              required
              className="form-input"
            />
            <input
              type="text"
              placeholder="Brand"
              value={product.brand}
              onChange={(e) => setProduct({...product, brand: e.target.value})}
              className="form-input"
            />
          </div>
          
          <div className="form-row">
            <input
              type="text"
              placeholder="Batch Number"
              value={product.batchNumber}
              onChange={(e) => setProduct({...product, batchNumber: e.target.value})}
              className="form-input"
            />
            <input
              type="text"
              placeholder="SKU Preview"
              value={generatePreviewSKU()}
              disabled
              className="form-input sku-preview"
              title="Auto-generated SKU"
            />
          </div>

          <div className="form-row">
            <input
              type="number"
              placeholder="Quantity *"
              value={product.quantity}
              onChange={(e) => setProduct({...product, quantity: e.target.value})}
              required
              className="form-input"
            />
            <input
              type="number"
              placeholder="Price *"
              step="0.01"
              value={product.price}
              onChange={(e) => setProduct({...product, price: e.target.value})}
              required
              className="form-input"
            />
          </div>

          <select
            value={product.category}
            onChange={(e) => setProduct({...product, category: e.target.value})}
            className="form-input"
          >
            <option value="general">General</option>
            <option value="medicine">Medicine</option>
            <option value="food">Food</option>
            <option value="cosmetics">Cosmetics</option>
            <option value="electronics">Electronics</option>
            <option value="clothing">Clothing</option>
          </select>
        </div>

        <div className="form-section">
          <h4>Dates</h4>
          <div className="form-row">
            <div className="form-group">
              <label>Manufacturing Date</label>
              <input
                type="date"
                value={product.manufacturingDate}
                onChange={(e) => setProduct({...product, manufacturingDate: e.target.value})}
                className="form-input"
              />
            </div>
            <div className="form-group">
              <label>Expiry Date</label>
              <input
                type="date"
                value={product.expiryDate}
                onChange={(e) => setProduct({...product, expiryDate: e.target.value})}
                className="form-input"
              />
            </div>
          </div>
        </div>

        <div className="form-section">
          <button 
            type="button" 
            onClick={() => setShowAdvanced(!showAdvanced)}
            className="toggle-advanced"
          >
            {showAdvanced ? '▼' : '▶'} Advanced Information
          </button>
          
          {showAdvanced && (
            <>
              <textarea
                placeholder="Product Description"
                value={product.description}
                onChange={(e) => setProduct({...product, description: e.target.value})}
                className="form-input"
                rows="3"
              />
              <div className="form-row">
                <input
                  type="text"
                  placeholder="Supplier"
                  value={product.supplier}
                  onChange={(e) => setProduct({...product, supplier: e.target.value})}
                  className="form-input"
                />
                <input
                  type="text"
                  placeholder="Storage Location"
                  value={product.location}
                  onChange={(e) => setProduct({...product, location: e.target.value})}
                  className="form-input"
                />
              </div>
            </>
          )}
        </div>

        <button type="submit" className="submit-button">Add Product Batch</button>
      </form>

      <style jsx>{`
        .add-product {
          max-width: 800px;
          margin: 0 auto;
          padding: 20px;
        }

        .product-form {
          background: #f8f9fa;
          padding: 20px;
          border-radius: 8px;
          border: 1px solid #dee2e6;
        }

        .form-section {
          margin-bottom: 20px;
          padding-bottom: 15px;
          border-bottom: 1px solid #e9ecef;
        }

        .form-section:last-child {
          border-bottom: none;
        }

        .form-section h4 {
          margin: 0 0 15px 0;
          color: #495057;
          font-size: 16px;
        }

        .form-row {
          display: flex;
          gap: 15px;
          margin-bottom: 15px;
        }

        .form-group {
          flex: 1;
        }

        .form-group label {
          display: block;
          margin-bottom: 5px;
          font-weight: 500;
          color: #495057;
          font-size: 14px;
        }

        .form-input {
          flex: 1;
          padding: 12px;
          border: 1px solid #ced4da;
          border-radius: 4px;
          font-size: 14px;
        }

        .form-input:focus {
          outline: none;
          border-color: #80bdff;
          box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
        }

        .sku-preview {
          background-color: #e9ecef;
          color: #6c757d;
          font-family: monospace;
        }

        .toggle-advanced {
          background: none;
          border: none;
          color: #007bff;
          cursor: pointer;
          font-size: 14px;
          margin-bottom: 15px;
        }

        .toggle-advanced:hover {
          text-decoration: underline;
        }

        .submit-button {
          width: 100%;
          padding: 12px;
          background-color: #007bff;
          color: white;
          border: none;
          border-radius: 4px;
          font-size: 16px;
          cursor: pointer;
        }

        .submit-button:hover {
          background-color: #0056b3;
        }

        @media (max-width: 768px) {
          .form-row {
            flex-direction: column;
          }
        }
      `}</style>
    </div>
  );
};

export default AddProduct;